@import url('https://fonts.googleapis.com/css2?family=Fira+Sans:wght@200&family=Nunito:wght@200;400&family=Rubik:ital,wght@1,300&display=swap');





* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: 'Nunito', sans-serif;
    background-color: #1a0900;
    color: #fff;
  }

  .coin-app {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 64px;
    color: #fff;
  }

  .coin-search {
    margin-bottom: 64px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
  }

  .coin-text {
    margin-bottom: 32px;
    text-align: center;
  }

  .coin-input {
    padding-left: 16px;
    width: 300px;
    height: 50px;
    border-radius: 4px;
    border: none;
    background-image: linear-gradient(
      -225deg,
      #ff9999 0%,
      #ff8080 48%,
      #ff6666 100%
    );
  
    color: #fffdfd;
  }

  .coin-input::placeholder {
    color: #e2e2e2;
  }